import {isEmpty} from '../../utils/validator'

var initialState = {
	isLogin: false,
	loginWay: 'phone',
	user: {},
  isVerify: true,  // 
  address: []  // 
}
const authReducer = (state = initialState, action) => {
	switch (action.type) {
		case 'SET_LOGIN_WAY':
			return {
				...state,
				loginWay: action.payload
			}
		case 'SET_CURRENT_USER': //  App.js  localStorage token 
			return {
				...state,
				isLogin: !isEmpty(action.payload),
				user: action.payload,
        isVerify: false  //  auth.isVerifu = false ()
			}
		default:
			return state;
	}
}
export default authReducer;
